import * as React from "react"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  header = (
    <nav class="navbar navbar-inverse">
      <Link className="nav navbar-nav header-link-home" to="/">
        Home
      </Link>
      <Link className="nav navbar-nav header-link-home" to="/article">
        Serials
      </Link>
      <Link className="nav navbar-nav header-link-home" to="/about">
        About
      </Link>
    </nav>
  )

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header">{header}</header>
      <main>{children}</main>
      <footer>
        © {new Date().getFullYear()} wusung. All rights reserved.
        {` `}
      </footer>
    </div>
  )
}

export default Layout
